import { PropsWithChildren, ReactNode, useId } from 'react';
import ReactModal from 'react-modal';

import Footer from 'components/layout/Footer';
import Typography from 'components/ui/Typography';
import Icon from 'components/ui/Icon';
import ButtonGroup from 'components/ui/ButtonGroup';
import Button from 'components/ui/Button';

import classNames from 'classnames';
import './index.scss';

export type Variant = 'inline' | 'full';

export interface ModalProps extends PropsWithChildren {
  variant?: Variant;
  contentLabel?: string;
  isOpen: boolean;
  onRequestClose?: () => void;
  shouldCloseOnOverlayClick?: boolean;
  onAfterOpen?: () => void;
  onAfterClose?: () => void;
}

const CONTAINER_CLASS_NAME_MAPS = {
  inline:
    'flex flex-col relative overflow-hidden min-w-[25rem] w-fit h-fit max-h-[calc(100dvh-48px)] mx-auto bg-white rounded-card-lg outline-none tablet:max-w-[60rem]',
  full: 'flex flex-col relative w-full h-full bg-white outline-none'
};

const CONTENT_CLASS_NAME_MAPS = {
  inline: 'grow flex flex-col',
  full: 'grow flex flex-col w-full h-full'
};

export interface ModalHeaderProps {
  onClose?: () => void;
  variant?: Variant;
  title?: ReactNode;
  canBeCustom?: boolean;
  hasShadow?: boolean;
}

export const ModalHeader = ({
  onClose,
  title,
  canBeCustom = false,
  hasShadow = false
}: ModalHeaderProps) => {
  return (
    <div
      className={classNames(
        'flex items-center shrink-0 bg-white',
        hasShadow
          ? 'h-16 pr-2 pl-[1.875rem] border-b rounded-card-md shadow-[0_0.25rem_0.5rem_0_#00000024]'
          : 'pt-5 pl-6',
        {
          'pr-[3.75rem]': onClose && !hasShadow
        },
        {
          'pr-6': !onClose && !hasShadow
        }
      )}
    >
      {title &&
        (canBeCustom ? (
          <>{title}</>
        ) : hasShadow ? (
          <div className="inline-flex px-4">
            <Typography variant="head" color="text-gray-800">
              {title}
            </Typography>
          </div>
        ) : (
          <Typography size="lg" weight={700} color="text-gray-800">
            {title}
          </Typography>
        ))}

      {onClose && (
        <div
          className={classNames(
            'inline-flex items-center justify-center shrink-0',
            hasShadow ? 'ml-auto' : 'absolute top-2 right-1'
          )}
        >
          <Icon
            name="icon-basic-close"
            size={hasShadow ? 'text-28' : 'text-24'}
            color="text-gray-600"
            onClick={onClose}
          />
        </div>
      )}
    </div>
  );
};

export interface ModalBodyProps extends PropsWithChildren {
  hasScrollbar?: boolean;
  isPaper?: boolean;
}

export const ModalBody = ({ hasScrollbar = false, isPaper = false, children }: ModalBodyProps) => {
  return (
    <div
      className={classNames(
        'flex flex-col grow pt-5 pb-6 px-6 scrollbar-custom',
        hasScrollbar ? 'overflow-y-scroll' : 'overflow-y-auto',
        { 'scrollbar-custom--paper': isPaper }
      )}
    >
      {children}
    </div>
  );
};

export interface ModalFooterProps {
  secondaryLabel?: string;
  onSecondaryClick?: () => void;
  primaryLabel?: string;
  onPrimaryClick?: () => void;
  useCustomElement?: ReactNode;
}

export const ModalFooter = ({
  secondaryLabel = '취소',
  onSecondaryClick,
  primaryLabel = '확인',
  onPrimaryClick,
  useCustomElement
}: ModalFooterProps) => {
  return (
    <div className={classNames('shrink-0 flex pb-6 px-6')}>
      <ButtonGroup isFullWidth>
        {useCustomElement ? (
          useCustomElement
        ) : (
          <>
            {secondaryLabel && onSecondaryClick && (
              <Button flex={1} character="modal" color="secondary" onClick={onSecondaryClick}>
                {secondaryLabel}
              </Button>
            )}
            {primaryLabel && onPrimaryClick && (
              <Button flex={1} character="modal" onClick={onPrimaryClick}>
                {primaryLabel}
              </Button>
            )}
          </>
        )}
      </ButtonGroup>
    </div>
  );
};

const Modal = ({
  variant = 'inline',
  contentLabel,
  isOpen = false,
  onRequestClose,
  shouldCloseOnOverlayClick = false,
  onAfterOpen,
  onAfterClose,
  children
}: ModalProps) => {
  const id = useId();

  const RequestClose = () => {
    if (variant !== 'full' && onRequestClose) {
      onRequestClose();
    }
  };

  return (
    <ReactModal
      id={`${variant}-modal-${id}`}
      role={`${variant}-modal`}
      isOpen={isOpen}
      contentLabel={contentLabel || '어떤 레이블인지 설명해주세요'}
      portalClassName="z-auto"
      ariaHideApp={false}
      onAfterOpen={onAfterOpen}
      onAfterClose={onAfterClose}
      onRequestClose={RequestClose}
      overlayClassName="flex items-center justify-center fixed w-full h-full inset-0 z-[9999] bg-black/50"
      contentElement={(props) => (
        <div
          className={classNames(
            'content-ele flex flex-col bg-white',
            CONTENT_CLASS_NAME_MAPS[variant]
          )}
          {...props}
        >
          {children}
        </div>
      )}
      shouldCloseOnOverlayClick={shouldCloseOnOverlayClick && variant !== 'full'}
      className={classNames('qb-modal__content', CONTAINER_CLASS_NAME_MAPS[variant])}
    />
  );
};

Modal.Header = ModalHeader;
Modal.Body = ModalBody;
Modal.Footer = ModalFooter;
Modal.Copyright = Footer;

export default Modal;
